.part-box{padding: 1rem 0;}
.section-form{margin-bottom: 0px;}

.btn{
	background: #fcc800;
}

.titlebox{
	margin-top: 20px;
	margin-bottom: 20px;
}

.titlebox .line{
	background: #fcc800;
}

/*薪资*/
.part-xinzi img{
	width: 100%;
	display: block;
}
.part-xinzi .btn{
	width: 40%;
}
