	padding-top:20px; 
}



/*问题*/
.part-wenti{
	background: url(/assets/images/jingjiadt/ruanzhuang/wenti.jpg)no-repeat center;
	background-size: 100%;
}
.part-wenti .titlebox h1{
	color:#fff;
}
.wenti-item{
	margin-bottom: 0.15rem;
	
} 
.wenti-lastitem{
	margin-bottom: 0;
}
.wenti-item p{
	font-size: 0.32rem;
	color: #fff;
	line-height: 0.3rem;
	float: left;
	padding-top: 10px;
	padding-bottom: 10px
}
.wenti-item p:nth-of-type(1){
	width: 72%;
	padding-left: 3%;
	border: 1px solid #5e5e66;
	border-right: none;
}
.wenti-item p:nth-of-type(2){
	width: 24%;
	text-align: center;
	background: #fcc800;
	border: 1px solid #fcc800;
	color: #333;
}



/*教学保障*/
.part-jxbz .titlebox h1{
	line-height: 0.3rem;
}
.jxbz-item{
	float:left;
	text-align: center;
	width: 33.33%;
}
.jxbz-item img{
	width:52%;
	margin: 0 auto;
	display: block;
}
.jxbz-threeitem{
	float:right;
}
.jxbz-item h2{
	color:#333;
	font-size: 0.14rem;
	line-height: 0.14rem;
	margin-top: 0.1rem;
}
.jxbz-ctn{
	margin-top: 0.15rem;
}

